      let newTime = newDate.getFullYear() + '-' + month + '-' + newDate.getDate();
      // this.setData({
      //   newTime: newTime
      // });
	  this.newTime = newTime
    },
   toSingleAllo:function(companyCode){
   	_this.codeValue = []
